import React from 'react'
import { Carousel } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import IMG1 from '../../../statics/background1.jpg';
import IMG2 from '../../../statics/background2.jpg';
import IMG3 from '../../../statics/background3.jpg';
import './mainsection.css'
const Mainsection = () => {
    return (
        <>           
            <Carousel className='test' >
                <Carousel.Item interval={1000}>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG1}
                    alt="First slide"
                    />
                    <Carousel.Caption>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={500}>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG2}
                    alt="Second slide"
                    />
                    <Carousel.Caption>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG3}
                    alt="Third slide"
                    />
                    <Carousel.Caption>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </>
    )
}

export default Mainsection
