import styled from 'styled-components';
import { Link as LinkR } from 'react-router-dom';
import Logo from '../../statics/logo.png'
import Background from '../../statics/background_16.jpg'
export const HeaderWrapper = styled.div`
    width:100%;
    overflow:hidden;
    box-sizing:border-box;
    /* background-color:white; */
    box-shadow: 0px 0px 5px 5px #000;
    background-image: url(${Background});
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
    z-index:5;
    margin-bottom:20px;
`;
export const HeaderContainer = styled.div`
    width:70%;
    display:flex;
    justify-content:space-between;
    margin:0 auto;
    padding:40px 0;
    align-items:center;
    /* background-color:white; */
    @media screen and (max-width:960px){
        width: 80%;
    }
`;
export const MobIcon = styled.div`
    display:none;
    @media screen and (max-width:960px){
        line-height:12px;
        color:#fff;
        top:10px;
        right:20px;
        padding:3px 3px;
        display:${({isOpen}) => (isOpen ? 'none' :'block')};;
        font-size:55px;
        cursor: pointer;
        transition:0.3s all ease-in-out;
        border:${({isOpen}) => (isOpen ? 'none' :'1.5px solid #fff')};
        border-radius:5px;
        z-index:999;
        /* position:${({isOpen}) => (isOpen ? 'fixed' :'')}; */
    }
`;
export const HeaderIcon = styled.div`
    justify-self:start;
`;
export const IconLink = styled.div`
    background:url(${Logo});
    background-size:contain;
    width:120px;
    height:120px;
    /* border-left:2.5px solid #fff; */
    display:block;
`;
export const HeaderNav = styled.div`
    width:65%; 
    display:grid;
    grid-template-columns:repeat(6,16%);
    @media screen and (max-width:960px){
        display:none;
    }
`;
export const HeaderItem = styled.div`
    text-align:center;
    white-space:nowrap;
`;
export const ItemLink = styled(LinkR)`
    text-decoration:none;
    color:#fff;
    font-size:15px;
    font-weight:800;
    &.active{
        padding:5px 5px;
        border-bottom:2px solid #CCCC33; 
    }
    &:hover{
        color:#fff;
    }
    
`;