import React from 'react'

import {
    HomestayIntroWrapper,
    HomestayImg,
    HomestayData,
    ReadMoreBtn,
} from '../style'
const HomestaySection = ({ id, img, title, dsc, btnchange}) => {
    return (
        <>
        <HomestayIntroWrapper>
            <HomestayImg src={img} />
            <HomestayData>
                <h2>{title}</h2>
                <p>{dsc}</p>
                <ReadMoreBtn btnchange={btnchange ? 1: 0} to={'/roomintroduction/'+id}>查看詳情</ReadMoreBtn>
            </HomestayData>
        </HomestayIntroWrapper>
        </>
    )
}

export default HomestaySection
