import styled from 'styled-components';
import { Link as LinkR } from 'react-router-dom';
import Background from '../../statics/background_6.jpg'
export const SideBarWrapper = styled.div`
    position:fixed;
    top:0;
    width:80%;
    height:100%;
    z-index:999;
    background-image: url(${Background});
    box-sizing:border-box;
    transition:0.3s all ease-in-out;
    opacity:${({isOpen}) => (isOpen ? '100%' : '0')};
    right:${({isOpen}) => (isOpen ? '0' :'-100%')};
    overflow: hidden;
`;
export const OpacityBlock = styled.div`
    position:fixed;
    width:20%;
    height:100%;
    background-color: #000;
    top:0;
    left:0;
    opacity:80%;
    display:${({isOpen}) => (isOpen ? 'block' :'none')};
`;

export const SideBarMenu = styled.ul`
    display:grid;
    grid-template-rows:repeat(6, 1fr);
    box-sizing:border-box;
    margin-top:150px;
    @media screen and (max-width:768px){
        margin-top:170px;
    }
`;
export const MobIcon = styled.div`
    display:none;
    @media screen and (max-width:960px){
        line-height:12px;
        color:#fff;
        top:70px;
        right:90px;
        padding:3px 3px;
        display:${({isOpen}) => (isOpen ? 'block' :'none')};;
        font-size:55px;
        cursor: pointer;
        transition:0.3s all ease-in-out;
        /* border:${({isOpen}) => (isOpen ? 'none' :'1.5px solid #fff')}; */
        /* border-radius:5px; */
        z-index:999;
        position:${({isOpen}) => (isOpen ? 'fixed' :'')};
    }
    @media screen and (max-width:768px){
        right:60px;
    }
    @media screen and (max-width:480px){
        right:40px;
    }
`;
export const SideBarItem = styled.li`
    padding-left:150px;
    white-space: nowrap;
    @media screen and (max-width:768px){
        padding-left:90px;
    }
    @media screen and (max-width:480px){
        padding-left:65px;
    }
`;
export const SideBarLink = styled(LinkR)`
    color:#000;
    text-decoration:none;
    font-size:23px;
    display:inline-block;
    padding:23px 70px 23px 0;
    font-weight:bold;
    font-size:25px;
    &.active{
        color:#fff;
    }
    @media screen and (max-width:768px){
        font-size:22px;
    }
    @media screen and (max-width:480px){
        font-size:20px;
    }
`;

    
    