import React from 'react';
import { IoIosClose } from 'react-icons/io';
import { useLocation } from "react-router-dom";
import {
    SideBarWrapper,
    OpacityBlock,
    SideBarMenu,
    MobIcon,
    SideBarItem,
    SideBarLink
} from './style'

const SideBar = ({ isOpen, setOpen}) =>{
    const location = useLocation();
    const { pathname } = location;
    const splitLocation = pathname.split("/");
    const handleCloseSidebar =() =>{
        setOpen(false)
    }
    return (
        <SideBarWrapper isOpen={isOpen}>
            <OpacityBlock isOpen={isOpen}>
            </OpacityBlock>
            <SideBarMenu>
                <MobIcon isOpen={isOpen}>
                    <IoIosClose onClick={handleCloseSidebar} />
                </MobIcon>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === '' ? 'active' : '' } to='/' onClick={handleCloseSidebar}>民宿簡介</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'rules' ? 'active' : '' } to='/rules' onClick={handleCloseSidebar}>訂房須知</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'roomintroduction' ? 'active' : '' } to='/roomintroduction/default' onClick={handleCloseSidebar}>房型介紹</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'journey' ? 'active' : '' } to='/journey' onClick={handleCloseSidebar}>套裝行程</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'howtoplay' ? 'active' : '' } to='/howtoplay' onClick={handleCloseSidebar}>西螺好吃好玩</SideBarLink>
                </SideBarItem>
                <SideBarItem>
                    <SideBarLink className={ splitLocation[1] === 'transportation' ? 'active' : '' } to='/transportation' onClick={handleCloseSidebar}>交通資訊</SideBarLink>
                </SideBarItem>
            </SideBarMenu>
        </SideBarWrapper>
    )
    }   

export default SideBar
